<template>
  <div class="box">
    <header>
        <NavBar blob="600">
            <template #title>收货地址</template>
            <template #go><van-icon name="arrow-left" /></template>
        </NavBar>
    </header>
    <main :class="{'bgcColor': !list[0]}">
      <div v-if="list[0]" >
        <div class="adress" v-for="item in list" :key="item.address_id">
          <h3>{{item.name}} {{item.phone}}</h3>
          <p>{{Object.values(item.region).join(' ') + ' ' + item.detail}}</p>
          <div class="controller">
            <div class="left">
              <van-radio checked-color="red" :name="item.address_id" v-model="radio" @click="setDefaultAdressAction"></van-radio>
              <span v-if="item.address_id === radio">默认</span>
              <span v-else>选择</span>
            </div>
            <div class="right">
              <div class="edit" @click="editTo(item.address_id)">
                <van-icon name="edit" /><span>编辑</span>
              </div>
              <div class="del" @click="removeAdressAction(item.address_id)">
                <van-icon name="delete-o" /><span>删除</span>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div v-else class="write">
        <img src="@/assets/空空如也.png" alt="">
        <span>亲，暂无收获地址</span>
      </div>
    </main>
    <footer>
        <button @click="addTo">添加收获地址</button>
    </footer>
  </div>
</template>

<script>
import { getAdressList, setDefaultAdress, getDefaultAdress, removeAdress } from '@/api/adress'
import { Toast } from 'vant'
import loginConfirm from '@/mixins/loginConfirm'
export default {
  name: 'adressIndex',
  mixins: [loginConfirm],
  data () {
    return {
      radio: 0,
      list: []
    }
  },
  methods: {
    async getAdressListAction () {
      const { data: { list } } = await getAdressList()
      this.list = list
    },
    async getDefaultAdressAction () {
      const { data: { defaultId } } = await getDefaultAdress()
      this.radio = defaultId
    },
    async setDefaultAdressAction () {
      await setDefaultAdress(this.radio)
      // 如果path等于pay点完默认就跳回
      const to = this.$store.state.addressTo
      if (to) this.$router.replace(to.fullPath)
    },
    async removeAdressAction (id) {
      const { message } = await removeAdress(id)
      Toast(message)
      this.getAdressListAction()
    },
    editTo (id) {
      const to = this.$store.state.addressTo
      // 点击编辑判断是否在pay页面，如果在pay页面则跳转时添加上token便于判断是否跳转pay页面
      if (to) {
        this.$router.replace(`/adressedit?id=${id}&token=888`)
      } else {
        this.$router.replace(`/adressedit?id=${id}`)
      }
    },
    addTo () {
      if (!this.loginConfirm()) return
      const to = this.$store.state.addressTo
      // 点击编辑判断是否在pay页面，如果在pay页面则跳转时添加上token便于判断是否跳转pay页面
      if (to) {
        this.$router.replace('/adressedit?token=888')
      } else {
        this.$router.replace('/adressedit')
      }
    }
  },
  created () {
    if (!this.loginConfirm()) return
    this.getAdressListAction()
    this.getDefaultAdressAction()
  }
}
</script>

<style lang="less" scoped>
.write {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 70px 0 0;
  img {
    width: 200px;
  }
  font-size: 16px;
  color: gray;
}
.bgcColor{
  background-color: #fafafa;
}
main {
  overflow: auto;
  padding:10px 10px 0;
    position: fixed;
    top:50px;
    left: 0;
    right: 0;
    bottom: 60px;
    font-size: 14px;
    .adress {
      margin-bottom: 10px;
      background: #fff;
      border-radius: 10px;
      padding: 20px;
      h3 {
        font-size: 16px;
        font-weight:400 ;
      }
      p {
        margin-top: 6px;
      }
      .controller {
        margin-top: 20px;
        display: flex;
        justify-content: space-between;
        .left, .right {
          display: flex;
          align-items: center;
        }
        .left {
          span {
            margin-left: 10px;
          }
        }
        .right {
          .edit {
            margin-right: 12px;
          }
        }
      }

    }
}
footer {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    height: 60px;
    background: #fff;
    padding: 10px 20px;
    button {
      font-size: 16px;
      width: 100%;
      height: 100%;
      background: linear-gradient(to right, #fe361a, #fc6f37);
      border: 0;
      border-radius: 20px;
      color: #ececec;
    }
}
</style>
